  --primary-brand-color: #f87719;
  --dark-theme-background-color: #111;
  --light-theme-background-color: #fff;
  --primary-text-color: #fff;
  --secondary-text-color: #f8f8f8;
  --secondary-black-color: #282828;
  --dark-theme-grey-color: #b7b7b7;
  --light-theme-grey-color: #595959;
  --primary-button-color: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --stars-color: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
